* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
  }
  
  a {
    text-decoration: none;
    color: #fff;
  }
  
  ul,
  ol {
    list-style: none;
  }
  
  table {
    border-spacing: 0;
  }
  
  input,
  button,
  select,
  textarea {
    border: 0;
    outline: none;
  }